import React from 'react'
import { SectionTemplate } from 'src/components'
import { whatsappIcon, telegramIcon } from 'src/assets'
import './Contacts.css'

export const Contacts = () => {
  return (
    <SectionTemplate title='' backgroundColor='grey' id='contacts' >
      <div className='contacts'>
        <div className="contacts__info">
          <h5></h5>
          <h3> </h3>
          <div className="contacts__icons">
            <div className="contacts__icon">
              <img className='icon__image' src={telegramIcon} />
              <p className='icon__text'>@username</p>
            </div>
            <div className="contacts__icon">
              <img className='icon__image' src={whatsappIcon} />
              <p className='icon__text'>@username</p>
            </div>
          </div>
        </div>
        <form className='contacts__form'>
          <h4 className='form__title'>  </h4>
          <p className='form__subtitle'>          .  .</p>
          <div className="form__inputs">
            <input type="text" placeholder='' />
            <input type="email" placeholder='' />
            <input type="text" placeholder='' />
          </div>
          <button className='form__button btnBlack'></button>
          <a href="#" className='form__policy'>  ,       . </a>
        </form>
      </div>
    </SectionTemplate>
  )
}
